.globe {
    margin: 0 auto;
    width: 200px;
    height: 200px;
    transform-style: preserve-3d;
    -webkit-animation-name: animateWorld;
            animation-name: animateWorld;
    -webkit-animation-duration: 30s;
            animation-duration: 30s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
}

.circle {
    position: absolute;
    width: 100%;    
    height: 100%;
    border: 2px dashed #009664;
    border-radius: 50%;
    background: rgba(255, 255, 255, 0.212);
}

.circle-zero {
    transform: rotateX(90deg);
}

.circle-two {
    transform: rotateY(45deg);
}

.circle-three {
    transform: rotateY(90deg);
}

.circle-four {
    transform: rotateY(135deg);
}

.circle-five {
    width: 173px;
    height: 173px;
    margin: 14px;
    transform: rotateX(90deg) translateZ(50px);
}

.circle-six {
    width: 173px;
    height: 173px;
    margin: 14px;
    transform: rotateX(90deg) translateZ(-50px);
}

@-webkit-keyframes animateWorld {
    0% { transform: rotateY(0deg) rotateX(0deg) rotateZ(0deg); }
    50% { transform: rotateY(360deg) rotateX(180deg) rotateZ(180deg); }
    100% { transform: rotateY(720deg) rotateX(360deg) rotateZ(360deg); }
}

@keyframes animateWorld {
    0% { transform: rotateY(0deg) rotateX(0deg) rotateZ(0deg); }
    50% { transform: rotateY(360deg) rotateX(180deg) rotateZ(180deg); }
    100% { transform: rotateY(720deg) rotateX(360deg) rotateZ(360deg); }
}
