svg {
    touch-action: none;
}

.jvectormap-container {
    width: 100%;
    height: 100%;
    position: relative;
    overflow: hidden;
    touch-action: none;
}

.jvectormap-tip {
    position: absolute;
    display: none;
    border: solid 1px #CDCDCD;
    border-radius: 3px;
    background: #292929;
    color: white;
    font-family: sans-serif, Verdana;
    font-size: smaller;
    padding: 3px;
}

.jvectormap-zoomin, .jvectormap-zoomout, .jvectormap-goback {
    position: absolute;
    left: 10px;
    border-radius: 3px;
    background: #292929;
    padding: 3px;
    color: white;
    cursor: pointer;
    line-height: 10px;
    text-align: center;
    box-sizing: content-box;
}

.jvectormap-zoomin, .jvectormap-zoomout {
    width: 10px;
    height: 10px;
}

.jvectormap-zoomin {
    top: 10px;
}

.jvectormap-zoomout {
    top: 30px;
}

.jvectormap-goback {
    bottom: 10px;
    z-index: 1000;
    padding: 6px;
}

.jvectormap-spinner {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background: center no-repeat url(data:image/gif;base64,);
}

.jvectormap-legend-title {
    font-weight: bold;
    font-size: 14px;
    text-align: center;
}

.jvectormap-legend-cnt {
    position: absolute;
}

.jvectormap-legend-cnt-h {
    bottom: 0;
    right: 0;
}

.jvectormap-legend-cnt-v {
    top: 0;
    right: 0;
}

.jvectormap-legend {
    background: black;
    color: white;
    border-radius: 3px;
}

.jvectormap-legend-cnt-h .jvectormap-legend {
    float: left;
    margin: 0 10px 10px 0;
    padding: 3px 3px 1px 3px;
}

.jvectormap-legend-cnt-h .jvectormap-legend .jvectormap-legend-tick {
    float: left;
}

.jvectormap-legend-cnt-v .jvectormap-legend {
    margin: 10px 10px 0 0;
    padding: 3px;
}

.jvectormap-legend-cnt-h .jvectormap-legend-tick {
    width: 40px;
}

.jvectormap-legend-cnt-h .jvectormap-legend-tick-sample {
    height: 15px;
}

.jvectormap-legend-cnt-v .jvectormap-legend-tick-sample {
    height: 20px;
    width: 20px;
    display: inline-block;
    vertical-align: middle;
}

.jvectormap-legend-tick-text {
    font-size: 12px;
}

.jvectormap-legend-cnt-h .jvectormap-legend-tick-text {
    text-align: center;
}

.jvectormap-legend-cnt-v .jvectormap-legend-tick-text {
    display: inline-block;
    vertical-align: middle;
    line-height: 20px;
    padding-left: 3px;
}

.map-container {
    position: relative;
}

.map-key-wrapper {
    position: absolute;
    bottom: 7vw;
    right: 0;
}

.slider-container {
    padding: 1.5rem;
}

.slider-bar {
    height: 0.7rem;
    appearance: none;
    background-color: #dfdfdf;
    cursor: pointer;
    border-radius: 50px;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
}

.slider-bar::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    height: 20px;
    width: 20px;
    border: 1px solid #ffffff;
    border-radius: 50%; 
    background: #009664;
    cursor: pointer;
}

.slider-bar::-moz-range-thumb {
    appearance: none;
    height: 20px;
    width: 20px;
    border: 1px solid #ffffff;
    border-radius: 50%;
    background: #009664;
    cursor: pointer;
}


.lower-more-info-container {
    margin: 0 auto;
    padding-top: 30px;
    width: 100%;
}

.lower-element {
    padding: 1.5rem;
    margin-bottom: 30px;
    margin-right: 10px;
    margin-left: 10px;
    box-shadow: #63636333 0px 2px 8px 0px;
    border-radius: 30px;
    width: 100%;
    text-align: center;
}

.graph-container {
    text-align: center;
}

.globe-container {
    padding: 3.7rem;
}

.more-info-button {
    text-decoration: none;
    margin-top: 20px;
    color: #ffffff;
    background-color: #009664;
    border-color: #00966448;
    border-radius: 50px;
    text-decoration: none;
}

.more-info-button:hover {
    color: #124512;
    border-color: #124512;
}

.carousel-indicators li{
    background-color: #009664;
    z-index: 2;
    border-radius: 50%;
    width: 10px;
    height: 10px;
}

.more-info-carousel-item {
    padding-top: 0.3rem;
    padding-bottom: 1rem;
    margin-bottom: 1rem;
}

.row {
    margin: 0;
}

.map-key {
    padding: 10px;
    text-align: center;

    box-shadow: #63636333 0px 2px 8px 0px;
    border-radius: 30px;
}

.key {
    width: 5em;
    margin-bottom: 5px;
    border-radius: 30px;
}